.TodoEditor {
  width: 600px;
  margin-top: 30px;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  flex-direction: column;
  padding: 16px;
  border: 1px solid black;
  border-radius: 4px;
  margin-bottom: 20px;
}

.TodoEditor__textarea {
  line-height: 16px;

  height: line-height * 3 + 24px;
  padding: 12px 16px;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;

  font: inherit;
  line-height: line-height;
  letter-spacing: 0.01em;

  resize: none;
  margin-bottom: 12px;
}

.TodoEditor__button {
  width: 100%;
  margin-left: auto;
  border-radius: 4px;
  padding: 12px 24px;
  background-color: indigo;
  color: white;
  font-weight: 500;
}
